$themeColor:skyblue;

.App {
  background: skyblue;
  height:100vh;
  h1{
    color: #fff;
    font-size: 80px;
    text-align: center;
    margin:0px;
    padding-top: 50px;
  }
}
.todo-list{
  width: 300px;
  background: #fff;
  border-radius: 10px;
  margin: 0 auto;
  padding: 20px 20px 30px;
  input{
    width: 60%;
    padding: 3px 10px;
  }
}
.todo-item{
  padding: 3px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  button{
    color: $themeColor;
    border:0;
    box-shadow: none;
  }
}